@use "./mixins/font-face";

@include font-face.font-face("Open Sans", "../fonts/OpenSansBold", 700, normal);
@include font-face.font-face(
  "Open Sans",
  "../fonts/OpenSansLight",
  300,
  normal
);
@include font-face.font-face(
  "Open Sans",
  "../fonts/OpenSansLightItalic",
  300,
  normal
);
@include font-face.font-face(
  "Open Sans",
  "../fonts/OpenSansRegular",
  400,
  normal
);
@include font-face.font-face(
  "Open Sans",
  "../fonts/OpenSansSemiBold",
  600,
  normal
);
@include font-face.font-face("Roboto", "../fonts/RobotoRegular", 400, normal);
@include font-face.font-face("Rubik", "../fonts/RubikBold", 700, normal);
@include font-face.font-face("Rubik", "../fonts/RubikMedium", 500, normal);
@include font-face.font-face("Rubik", "../fonts/RubikRegular", 400, normal);
