:root {
  // base
  --font-family: "Open Sans", sans-serif;
  --second-family: "Rubik", sans-serif;
  --third-family: "Roboto", sans-serif;
  --container-width: calc(1140px + (390px * 2));

  // colors
  --light-color: #fff;
  --dark: #0c122d;
  --grey: #575c74;
  --blue: #1b5fc1;
  --accent: #ffb800;
}
